












































































































import { AppService } from "@/service/app.service";
import Vue from "vue";
import Component from "vue-class-component";
import UploadImage from "@/x-vue/components/file/UploadImage.vue";
import { RequestData } from "@/x-vue/interfaces/interfaces";
import { ApiService } from "@/x-vue/services/api.service";
@Component({
  components: { UploadImage },
})
export default class CafeAdminEdit extends Vue {
  app = AppService.instance;

  async onSubmit(): Promise<void> {
    const options: RequestData = {
      idx: this.$store.state.currentCafe.idx,
      title: this.$store.state.currentCafe.title,
      description: this.$store.state.currentCafe.description,
      subcategories: this.$store.state.currentCafe.subcategories,
      app_name: this.$store.state.currentCafe.app_name,
      app_background_color: this.$store.state.currentCafe.app_background_color,
    };
    try {
      console.log("options; ", options);
      const cafe = await ApiService.instance.cafeUpdate(options);
      this.$store.commit("currentCafe", cafe);
      this.app.alert("Cafe Update", "Cafe update success");
    } catch (e) {
      this.app.error(e);
    }
  }
  reloadCafe(): void {
    this.app.loadCurrentCafeCategorySettings();
  }
}
